import React, {Component} from 'react';

class Signature extends Component {
    render(){
        let isaLine = <span></span>
        if(this.props.isa !== ''){
            isaLine = (
                <p style={{margin:'0in', marginBottom: '.0001pt', fontSize:'15px', fontFamily:'"Calibri",sans-serif'}}>
                    <span style={{fontSize:'13px'}}>ISA Certified Arborist {this.props.isa}</span>
                </p>
            )
        }

        let mobileLine = <span></span>
        if(this.props.mobile !== ``){
            mobileLine = <span style={{fontSize:'13px'}}>m: {this.props.mobile}</span>
        }

        let officeLine = <span></span>
        if(this.props.office !== ''){
            officeLine = <span style={{fontSize:'13px'}}>o: {this.props.office}</span>
        }

        let mSpace = <span></span>
        if(this.props.mobile !== '' && this.props.office !== ''){
            mSpace = <span>|</span>
        }
        return (
            <div>
                <p style={{margin:'0in', marginBottom: '.0001pt', fontSize:'15px', fontFamily:'"Calibri",sans-serif'}}>
                    <strong><span style={{fontSize:'13px', color:'#00703E'}}>{this.props.name}</span></strong><span style={{fontSize:'13px'}}>&nbsp;| {this.props.title}</span>
                </p>
                <p style={{margin:'0in', marginBottom: '.0001pt', fontSize:'15px', fontFamily:'"Calibri",sans-serif'}}>
                    {mobileLine} {mSpace} {officeLine} <span>|</span> <span><a href={"mailto:" + this.props.email}><span style={{color:'#0563C1'}}>{this.props.email}</span></a>&nbsp;| {this.props.city}, {this.props.state}<span>&nbsp;| <span><a href="http://www.eci-consulting.com/"><span style={{color:'#0563C1'}}>www.eci-consulting.com</span></a></span></span></span>
                </p>
                {isaLine}
                <p style={{margin:'0in', marginBottom: '.0001pt', fontSize:'15px', fontFamily:'"Calibri",sans-serif'}}>
                    <strong><em><span style={{fontFamily:'"Arial",sans-serif', color:'#00703E'}}><img width="257" src="data:image/jpeg;base64," alt="ECI Logo"></img></span></em></strong>
                </p>
                <p style={{margin:'0in', marginBottom: '.0001pt', fontSize:'15px', fontFamily:'"Calibri",sans-serif'}}><strong><em><span style={{fontSize:'13px', color:'#00703E'}}>&ldquo;Safely delivering sustainable solutions that are innovative by nature.&rdquo;</span></em></strong></p>
                 <p style={{margin:'0in', marginBottom: '.0001pt', fontSize:'15px', fontFamily:'"Calibri",sans-serif'}}><a href="https://www.facebook.com/ECIConsulting/"><img width="30" src="data:image/jpeg;base64," alt="Facebook" /></a><span>&nbsp; &nbsp;</span><a href="https://www.glassdoor.com/Overview/Working-at-Environmental-Consultants-EI_IE456522.11,36.htm"><img width="30" src="data:image/jpeg;base64," alt="Glassdoor" /></a><span>&nbsp; &nbsp;</span><a href="https://twitter.com/eciconsulting?lang=en"><img width="30" src="data:image/png;base64," alt="Twitter" /></a><span>&nbsp; &nbsp;</span><a href="https://www.instagram.com/environmentalconsultantsinc/"><img width="30" src="data:image/png;base64," alt="Instagram" /></a><span>&nbsp; &nbsp;&nbsp;</span><a href="https://www.linkedin.com/company/environmental-consultants-inc/"><img width="30" src="data:image/jpeg;base64," alt="Linkedin" /></a></p>
            </div>
        );
    }
}
 
export default Signature;
